<template>
    <section>
        <tg-list :IsShowAction="true" :showCheckBox="true"  ModuleName="Billing" :listType="[DetailListType]" 
        :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="meter_id" :HeaderText="$t('Meter')"
         :ListData="list" :HeaderData="Headers" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData" :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{data}">
                <template v-if="data.column.COLUMN_NAME == 'meter_name'">
                    <a class="text-center" @click="Deatils(data.row)">{{ data.row.meter_name }}</a>
                </template>
            </template>
         </tg-list>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import Schema from '../MeterManagement/MeterSchema'
export default({
    data(){
        return{
            Headers:[   
                {
                    COLUMN_NAME: "meter_name",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('MeterName'),
                    DISPLAY_ORDER: 1,
                    settings: {
                        isInSlot: true,
                    },
                }, {
                    COLUMN_NAME: "meter_model",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('ModelName'),
                    DISPLAY_ORDER: 2,
                },
                {
                    COLUMN_NAME: "service",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('MeterService'),
                    DISPLAY_ORDER: 3,
                },
                {
                    COLUMN_NAME: "unit",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('MeterUnit'),
                    DISPLAY_ORDER: 4,
                },
                {
                    COLUMN_NAME: "created_by",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CreatedBy'),
                    DISPLAY_ORDER: 5,
                },
                {
                    COLUMN_NAME: "created_at",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: this.$t('CreatedOn'),
                    DISPLAY_ORDER: 6,
                },
            
            ],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema, 
            listActions: Schema.listActions, 
            list:[],
            TotalRecords: 0,
            pageSize: 10,
            pageNum: 1,
            sortBy: '',
            sortExp: '',
            DetailListType: 'List',
            service_id:0,
            unit_id:0,
           searchCondition : ""


        }
    },
    created: function () {
        Schema.bindleftCommonSearchdropdown(this);
        this.FetchData();
        
    },
    methods:{
        FetchData(){
            DataService.GetMeterListing().then(response => {
                    if(response.data.length > 0){
                        this.list=response.data
                    }
                   
            })
        },
        leftCommonSearch(value){
            console.log("value",value)
            var vm = this;
            if (value.searchConditionJson.includes('service_id')) {
                vm.service_id = value.searchConditionJson;
            }else {
                vm.unit_id = value.searchConditionJson;
            }
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }            
            console.log("this.searchCondition",this.searchCondition)
            vm.FetchData();
        },
        pagerMethod(value){
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick(event){
            switch (event.callbackfunction) {
                case "Add":
                    this.AddNew();
                break;
                case "Delete":
                    this.Delete();
                    break;
            }
        },
        AddNew(){
            this.$router.push({path:'/ClientInvoice/AddMeter'});
        },
        Delete(){

        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
    }
})
</script>
