let listActions = [

    {
        title: 'Delete',
        callbackfunction: 'Delete',
        href: 'javascript:void(0)',
        icon: 'fa fa-trash text-danger',
        additionalSpan: ''
    },
    {
        title: 'View',
        callbackfunction: 'View',
        href: '',
        icon: 'fa fa-eye action-icon',
        additionalSpan: ''
    },
    {
        title: 'Edit',
        callbackfunction: 'Edit',
        href: '',
        icon: 'fa fa-pencil text-success action-icon',
        additionalSpan: ''
    },


];

function bindleftCommonSearchdropdown(instance) {
    var vm = instance;
    DataService.GetMeterServices().then(response => {
        response.data.forEach(item => {
            let obj1 = {
                name: item.key,
                value: item.value
            }
            vm.leftSearchFields[2].listOptions.push(obj1);
        })
    })
    DataService.GetMeterUnits().then(response => {
        response.data.forEach(item => {
            let obj1 = {
                name: item.key,
                value: item.value
            }
            vm.leftSearchFields[3].listOptions.push(obj1);
        })
    })
}

export default { leftsearchSchema, buttons, bindleftCommonSearchdropdown, listActions };